import React from 'react';
import BookImage from './book_image';

const BookDetail = (props) => {
  let { activeBook, newBook_A_C, books } = props;

    const handleAddBook = () => {
      const { activeBook, books, newBook_A_C } = this.props;

      if (!books.some(el => el.title === activeBook.title)) {
        newBook_A_C(activeBook);
      } else {
        window.alert('You already got this book in your library, hombre!');
      }
    }

    const renderBooks = () => {
      let markup;
      if (!activeBook) {
        markup = <h3>Please Select a Book.</h3>;
      } else {
        markup = (<div className="book-image-etc">
            <BookImage activeBook={activeBook}/>
            <div className="book-details">
              <h2 className="title"><span>Title:</span> {activeBook.title}</h2>
              <h2 className="pages"><span>Pages:</span> {activeBook.pages}</h2>
              <button onClick={ () => handleAddBook()}
                  className="btn btn-primary">Add Book to My Library</button>
            </div>
          </div>
        );
      }
      return markup;
    }
    return (
        <div id="book-details-ctonainer">
          {renderBooks()}
        </div>
    );
}

export default BookDetail;



